function MarqueeRow({
  clients,
  reverse = false,
  speed = 80,
}: {
  clients: string[];
  reverse?: boolean;
  speed?: number;
}) {
  const trackRef = useRef<HTMLDivElement>(null);
  const tweenRef = useRef<gsap.core.Tween | null>(null);

  useEffect(() => {
    const track = trackRef.current;
    if (!track) return;

    // Width of one half (the original set, not the duplicate)
    const halfWidth = track.scrollWidth / 2;
    const duration = halfWidth / speed;

    tweenRef.current = gsap.to(track, {
      x: reverse ? halfWidth : -halfWidth,
      duration,
      ease: "none",
      repeat: -1,
      modifiers: {
        x: (x) => {
          const v = parseFloat(x);
          if (reverse) {
            return (v % halfWidth) - halfWidth + "px";
          }
          return (v % -halfWidth) + "px";
        },
      },
    });

    return () => {
      tweenRef.current?.kill();
    };
  }, [reverse, speed]);

  const handleMouseEnter = () => tweenRef.current?.pause();
  const handleMouseLeave = () => tweenRef.current?.resume();

  // Triplicate for a seamless buffer
  const items = [...clients, ...clients, ...clients];

  return (
    <div
      className="relative flex overflow-hidden py-1"
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      {/* Fade edges */}
      <div className="absolute inset-y-0 left-0 w-24 bg-linear-to-r from-neutral-50 to-transparent z-10 pointer-events-none" />
      <div className="absolute inset-y-0 right-0 w-24 bg-linear-to-l from-neutral-50 to-transparent z-10 pointer-events-none" />

      <div
        ref={trackRef}
        className="flex whitespace-nowrap will-change-transform"
      >
        {items.map((client, idx) => (
          <span
            key={`${client}-${idx}`}
            className="inline-flex items-center gap-6 px-6"
          >
            <span className="text-lg md:text-xl font-medium text-neutral-500 hover:text-[#1B6CA8] transition-colors duration-300 cursor-default tracking-wide">
              {client}
            </span>
            <span
              className="w-1.5 h-1.5 rounded-full bg-neutral-300 shrink-0"
              aria-hidden="true"
            />
          </span>
        ))}
      </div>
    </div>
  );
}

export function ClientMarquee() {
  return (
    <section className="py-20 bg-neutral-50 overflow-hidden border-t border-neutral-100">
      <div className="container mx-auto px-6 mb-12 text-center">
        <p className="text-xs font-bold tracking-[0.25em] uppercase text-neutral-400 mb-3">
          Our Clients
        </p>
        <h2 className="text-3xl md:text-4xl font-light text-neutral-900 tracking-tight">
          Trusted by India&apos;s{" "}
          <span className="font-semibold text-[#1B6CA8]">
            Leading Organizations
          </span>
        </h2>
      </div>

      <div className="space-y-6">
        <MarqueeRow clients={CLIENTS_ROW_1} speed={60} />
        <MarqueeRow clients={CLIENTS_ROW_2} reverse speed={55} />
      </div>
    </section>
  );
}
